import { VNode, h } from 'vue'
import { TextComponentProps } from './defaultProps'
export interface PropToForm {
  component: string;
  subComponent?: string;
  extraProps?: { [key: string]: any };
  text?: string;
  options?: { text: string | VNode; value: any }[];
  initalTransform?: (v: any) => any;
  afterTransform?: (v: any) => any;
  afterTransform2?: (v: any) => any;
  valueProp?: string;
  eventName?: string;
}


export type PropsToForms = {
  [P in keyof TextComponentProps]?: PropToForm
}
const fontFamilyArr = [
  { text: '', value: '"SimSun","STSong"' },
  { text: '', value: '"SimHei","STHeiti"' },
  { text: '', value: '"KaiTi","STKaiti"' },
  { text: '', value: '"FangSong","STFangsong"' },
]
const fontFamilyOptions = fontFamilyArr.map(font => {
  return {
    value: font.value,
    text: <span style={{ fontFamily: font.value }}>{font.text}</span> as VNode
  }
})
const pxToNumberHandler: PropToForm = {
  component: 'a-input-number',
  initalTransform: (v: string) => parseInt(v),
  afterTransform: (e: number) => e ? `${e}px` : '',
  afterTransform2: (e: number) => e ? `${e}%` : '',
}

//
export const mapPropsToForms: PropsToForms = {
  text: {
    text: '',
    component: 'a-textarea',
    extraProps: { rows: 3 },
    afterTransform: (e: any) => e.target.value,
  },
  fontSize: {
    text: '',
    ...pxToNumberHandler
  },
  lineHeight: {
    text: '',
    component: 'a-slider',
    extraProps: { min: 0, max: 100, step: 10 },
    initalTransform: (v: string) => parseFloat(v),
    afterTransform: (e: number) => e.toString(),
  },
  textAlign: {
    component: 'a-radio-group',
    subComponent: 'a-radio-button',
    text: '',
    options: [
      { value: 'left', text: '' },
      { value: 'center', text: '' },
      { value: 'right', text: '' }
    ],
    afterTransform: (e: any) => e.target.value,
  },
  fontFamily: {
    component: 'a-select',
    subComponent: 'a-select-option',
    text: '',
    options: [
      { value: '', text: '' },
      ...fontFamilyOptions
    ]
  },
  width: {
    text: '',
    ...pxToNumberHandler
  },
  color: {
    component: 'color-picker',
    text: ''
  },
  backgroundColor: {
    component: 'color-picker',
    text: ''
  }
}